import server from '../../server.js';
export default {
	data() {
		return {
			topList:[],
			userinfo:[]
		}
	},
	onLoad:function(){
		this.getTop();
		this.userinfo=server.userinfo;
		
	},
	methods: {
		getBire:function(e){
			uni.navigateTo({
				url: '../i/bieren?auid='+e
			})
		},
		showtishi:function(){
			
			uni.showModal({
			    title: '关于排行榜',
			    content: '1、每周排行第一的用户可以获得3个月会员以及受邀加入专属微信群。\r\n2、每周排行第二名获得2个月会员。\r\n3、每周排行第三名获得1个月会员。\r\n4、每周记录时间为周日晚上23点59分。\r\n5、每周奖励发放时间为周一。',
				showCancel:false,
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
			
		},
		getTop:function(){
			uni.showLoading({
				title: '获取数据中'
			});
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=weekTop", //仅为示例，并非真实接口地址。
				data:{
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					
				},
				success: (res) => {
					if(res.data.code=="1"){
						this.topList=res.data.data
					}else{
						uni.showToast({
							title: "获取失败！",
							position:'bottom',
							icon:'none'
						});
					}
				},
				complete() {
					uni.hideLoading();
				}
			});
		}
		
	}
}
